.navbar{
    display:grid;
    grid-template-rows: 5fr 1fr 1fr 1fr 1fr 1fr;
    min-height: 100vh;
    transition-duration: 0.2s;
}

.nav-button{
    border-style: none;
    font-size: 15px;
    width: 100%;
    font-weight: bold;
    background-color: transparent;
}

.nav-button:hover{
    opacity: 0.5;
    cursor: pointer;
}

.picture-container{
    display: grid;
    grid-template-rows: 1fr 1fr;
    position: relative;
    width: 100%;
}

.wojak-picture{
    position: absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width:30%;
    border-radius: 50%;
    border-style: solid;
    border-color: white;
}

